  <div id="header">
    <div class="fixed-header">
      <div class="nav-button" @click="generalMenu()">
        <i class="fas fa-bars general-menu-open"></i>
        <i class="far fa-times-circle general-menu-close" style="display: none"></i>
      </div>
      <ul class="general-menu">
        <li>Test</li>
        <li>Test</li>
        <li>Test</li>
      </ul>
      <div class="logotype">
        Admin
      </div>
      <div class="account" @click="accountMenu()">
        <div class="account-name">
          {{name}}
        </div>
        <div class="account-avatar">
          <img :src="avatar" alt="">
        </div>
        <div class="account-nav-button">
          <i class="fas fa-caret-down"></i>
          <i class="fas fa-caret-up" style="display: none;"></i>
        </div>
        <ul class="account-menu">
          <li class="border">Настройки</li>
          <li class="logout" onClick="logout()">Выйти</li>
        </ul>
      </div>
    </div>
    <router-link :to="{ path: '/' }"><div id="go-auth"></div></router-link>
  </div>
</template>

<script>

export default {
  data() {
    return {
      avatar: localStorage.getItem('avatar'),
      name: localStorage.getItem('name')
    }
  },
  methods: {
    accountMenu() {
      if (document.getElementsByClassName('account-menu')[0].style.display == "" || document.getElementsByClassName('account-menu')[0].style.display == "none") {
        document.getElementsByClassName('account-menu')[0].style= "display: block";
      }else{
        document.getElementsByClassName('account-menu')[0].style= "display: none";
      }
    },
    generalMenu() {
      if (document.getElementsByClassName('general-menu')[0].style.display == "" || document.getElementsByClassName('general-menu')[0].style.display == "none") {
        document.getElementsByClassName('general-menu')[0].style= "display: block";
        document.getElementsByClassName('general-menu-open')[0].style= "display: none";
        document.getElementsByClassName('general-menu-close')[0].style= "display: block";
      }else{
        document.getElementsByClassName('general-menu')[0].style= "display: none";
        document.getElementsByClassName('general-menu-open')[0].style= "display: block";
        document.getElementsByClassName('general-menu-close')[0].style= "display: none";
      }
    }
  }
}

</script>

